*{
  margin:0;
  box-sizing: border-box;
  background: blanchedalmond;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.App{
  position: absolute;
  top:60%;
  left:100%;
  transform: translate(-100%,-50%);
  
  width: 50%;
  height: 80%;
  background: brown;
  color:rgb(62, 0, 90);
  
}
form{
  
  position: absolute;
  top:-20%;
  left: 40%;
  z-index: 10;

  transform: translateX(-50%);
}
input{
  position: relative;
  margin-left: 0%;
  margin-top: 10%;

  width: 20vw;
  height: 5vw;
  font-size: 20px;
  background: rosybrown;
  border-width: 0px;
  
}
input:hover{
  box-shadow:0 0 10px 1px crimson;
}
.todos{
  height: 100px;
  width: 100px;
  background: rgba(206, 78, 142, 0.884);
}
#butt{
 position: absolute;
  right: 0%;
  top:-20%;
  height: 10vh;
  width: 10vw;
  background:rosybrown;
  z-index: -1;
  
  color:wheat;
  font-size: 20px;
  border-width: 0px;
}
#butt:hover{
  box-shadow:0 0 10px 1px crimson;
}
#to{
  background:brown;
  size: 20vh;
  font-size: 20px;
  
  
  
}
#to>div{
  float: right;
  background: brown;
  margin-bottom: -1px;
  
  width: 90%;
  border:3px solid wheat;
  color:wheat;
  
}
#to>div:hover{
  background: aquamarine;
  color: black;
}
h1{

  font-size: 4vw;
  color: bisque;
  background: rosybrown;
}
#h1wrap{
  word-wrap: break-word;
  top:-20%;
  width: 20vw;
  height: 100%;
  position: fixed;
  left:-100%;
  text-align: center;
  word-wrap: break-word;
  overflow-wrap: break-word;

}h1:hover{
  background: brown;
}